* {
  font-family: Arial, Helvetica, sans-serif;

}

body {
  padding: 60px;
  background-color: rgb(26, 26, 56);
  color: #fff;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid rgb(136, 37, 37);
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
}

